	.css-selector {
    background: linear-gradient(122deg, #26e9b7, #20a4c6, #5279cd, #c91641, #580be6, #da760e);
    background-size: 1600% 1600%;

    -webkit-animation: changyBoi 21s ease infinite;
    -moz-animation: changyBoi 21s ease infinite;
    animation: changyBoi 21s ease infinite;
	}

	.bg-cover{
  box-shadow: 5px 10px 18px grey;
