<svelte:head>
    <link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/flatly/bootstrap.min.css">
    <script src="https://cdn.paddle.com/paddle/paddle.js" crossorigin="anonymous" SameSite="Secure"></script>
<script type="text/javascript">
	Paddle.Setup({ vendor: 116844 });
</script>
</svelte:head>
<style>
    .hidden {
            display: none;
        }  
    .pressDown {
        background-color: #5778f3;
        padding: 10px 30px;
        color: #fff;
        border-radius: 5px;
        border-bottom: 4px solid #3e4caf;
        transition: 0.1s;
    }

    .pressDown:hover{
        border-bottom-width: 0;
        margin-top: 3px;
        color: #fff;
    }
    
</style>
<script>
    let logo = 'https://i.imgur.com/5CHnbDm.png';

    export let displayName;
    export let email;
    export let emailVerified;
    //export let photoURL;
    export let uid;
    let checkoutId;
    let isPro = false;

  import App from './App.svelte';
  import { onMount } from 'svelte';
  import { Router, Link, link, Route } from "svelte-routing";
  import {Row, Alert,Modal, ModalBody, ModalFooter, ModalHeader, Table, Label, Container, Button, Col, Card, CardBody} from 'sveltestrap';
  
  let open = false;
  const toggle = () => (open = !open);

  import { auth, googleProvider, twitterProvider, db, firebaseAnalytics } from './firebase';
  let user = auth.currentUser;

  let totalctr=0, contentMadeSoFar=0;
  let timesave,fbctr=0,twctr=0,ldctr=0,otctr=0;

    function totalChecks(){
        (document.getElementById("fb").checked === true)? fbctr=1:fbctr=0;
        (document.getElementById("tw").checked === true)? twctr=1:twctr=0;
        (document.getElementById("ld").checked === true)? ldctr=1:ldctr=0;
        (document.getElementById("ot").checked === true)? otctr=1:otctr=0;
        totalctr=fbctr+twctr+ldctr+otctr;

        //code to check if user exists in userdb collection or not
        db.collection('usersDB').doc(uid).get().then(doc => {
        if (doc.exists) {
            //if user is already there, then save his totalctr
                let val = contentMadeSoFar+totalctr
                db.collection('usersDB').doc(uid).set({
                        totalCreated: val
                })
                contentMadeSoFar = val
                timesaved()
        } else {
            db.collection('usersDB').doc(uid).set({
                        totalCreated: val
                })
                contentMadeSoFar = val
                timesaved()
        }
        }).catch(function(error) {
            console.log("Error getting document:", error);
        });

        document.getElementById("today_date").classList.remove("text-danger");
        document.getElementById("today_date").classList.add("text-success");
        document.getElementById("contentStatusText").innerHTML = "Content created for today. Woohoo! ü•≥";
        document.getElementById("contentStatusText").classList.add("text-success");
        document.getElementById('btn_text').innerText = "Completed üòé";
        document.getElementById('btn_text').disabled = true;
        timesaved();
    }

    function timesaved(){
        timesave = contentMadeSoFar*2;
    }

    function toggleSuccessText()
    {
        var x = document.getElementById("successText");
        x.classList.toggle("hidden");
    }

    function sendEmailVerify(){
    user.sendEmailVerification().then(function() {
    // Email sent.
    toggleSuccessText()
    console.log("email verification sent");
    
    }).catch(function(error) {
    // An error happened.
    });
    }

    function checkoutClosed(data) {
  //  console.log(data);
    alert('Your purchase has been cancelled, we hope to see you again soon!');
    }

	function openMCheckout() {
        firebaseAnalytics.logEvent('popup_clicked_MonthlyCheckout', {
            paymentMethod: 'Monthly_Popup_opened'
        })
        Paddle.Checkout.open({ 
            product: 597953,
            email: email,
            completeDetails: true,
            closeCallback:"checkoutClosed",
            successCallback: function(data) {
                checkoutId = data.checkout.id;
                
                Paddle.Order.details(checkoutId, function(data) {
                // Order data, downloads, receipts etc... available within 'data' variable.
                console.log(data);

                //To call firebase db and add details so that we can know this is a pro user, by saving there checkout ID and email
                    db.collection('PROusers').doc(uid).set({
                        email: email,
                        checkoutId: checkoutId,
                        planType: "Monthly",
                        SubscriptionDate: Date()
                    })
                }).then(()=> {
                    setTimeout(function(){ alert("Payment Successful! Enjoy your PRO Access."); }, 2000);
                });
            }
        });
    }

    function openYCheckout() {
        firebaseAnalytics.logEvent('popup_clicked_YearlyCheckout', {
            paymentMethod: 'Yearly_Popup_opened'
        })
        Paddle.Checkout.open({ 
            product: 597966,
            completeDetails: true,
            email: email,
            successCallback: function(data) {
                checkoutId = data.checkout.id;
                
                Paddle.Order.details(checkoutId, function(data) {
                // Order data, downloads, receipts etc... available within 'data' variable.
                console.log(data);

                //To call firebase db and add details so that we can know this is a pro user, by saving there checkout ID and email
                    db.collection('PROusers').doc(uid).set({
                        email: email,
                        checkoutId: checkoutId,
                        planType: "Yearly",
                        SubscriptionDate: Date()
                    })
                }).then(()=> {
                    setTimeout(function(){ alert("Payment Successful! Enjoy your PRO Access."); }, 2000);
                });
            }
        });
    }

    function emailUserTransactions(){
        firebaseAnalytics.logEvent('Emailed_user_txn_info')
        Paddle.User.History(email, null, function(response) {					
            if(response.success) {
                // Let the user know they'll receive an email with their 
                // order history and license codes if their email was matched.
                alert(response.message)
            } else {
                // Likely a formatting error in the email, vendorID, or ProductID.
            }
            });
    }

    function backToHome(){
		window.location = "/";
	}

    function DayToday(val = 0) {
        let d = new Date();
        let days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
        return days[d.getDay()+val];
    }

    function DateToday(val=0) {
        let d = new Date();
        let todayDate = (d.getDate()+val)+"/"+(d.getMonth()+1)+"/"+d.getFullYear()
        return todayDate;
    }

    function thisWeek(val = 0){
        let d = new Date();
        let todayDate = ((d.getDate() - d.getDay())+val)+"/"+(d.getMonth()+1)+"/"+d.getFullYear()
        return todayDate;
    }

    onMount(() => {
        // CHECK IF THE USER EXISTS IN USERDB
        db.collection('usersDB').doc(uid).get().then(doc => {
        if (doc.exists) {
            let userData = doc.data();
            console.log(userData);
            contentMadeSoFar = userData.totalCreated;
            timesaved();
        } else {
            db.collection('usersDB').doc(uid).set({
                        totalCreated: 0
            })
        }
        }).catch(function(error) {
            console.log("Error getting document:", error);
        });
        
        document.getElementById("today_date").classList.add("text-danger"); 

        // Now to check if a user is PRO or NOT by checking PRO users database in firebase.
        db.collection('PROusers').doc(uid).get().then(doc => {
            if (doc.exists) {
        // console.log("Document data:", doc.data());
                isPro = true;
        } else {
            // doc.data() will be undefined in this case
        //  console.log("No such document!");
            isPro = false;
        }
        }).catch(function(error) {
            console.log("Error getting document:", error);
        });
        });

</script>
<Container fluid>
    
    {#if !emailVerified}
    <Row class="login text-center py-5">
        <Col sm="12" class="m-2">
            <img src={logo} width="250" height="250" alt="logo"/>
        </Col>
        <Col sm="12" class="m-2">
            <h4 class="text-primary font-weight-bold">Welcome, { displayName }!</h4>
        </Col>
        <Col sm="12" class="m-2">
            <h4>You are almost ready to get started with Designtack.</h4>
            <h4>Just click the button below to verify your email address!</h4>
        </Col>
        <Col sm="12" class="m-2">
            <Button on:click={sendEmailVerify} color="primary" size="lg">Verify Email Address üìß</Button>
        </Col>
        <Col sm="12" class="m-2">
            <h5 id="successText" class="hidden text-success">Email Verification Sent! Check for an email from Designtack :)</h5>
        </Col>
        <Col sm="12" class="m-2">
            <Button color="danger" on:click={ () => {
                auth.signOut()
                backToHome()
            } }>Log Out</Button>
        </Col>
    </Row>
    {:else}  
    <Row class="login text-center py-5">
        <Col sm="12" class="m-2">
            <h4 class="text-primary font-weight-bold">Welcome back, { displayName }!</h4>
        </Col>
        <Col sm="12" class="m-2">
            <a href="/App" class="pressDown btn btn-lg" use:link>Start Creating Content</a>
        </Col>
    </Row>
    <Row class="login text-center pt-2">
        <Col>
            <Row class="my-2">
                <Col sm="4">
                    <h4 class="text-primary font-weight-bold py-2">Daily Goal üéØ</h4>
                    <Card body>
                        <Col><h5 id="today_date" class="font-weight-bold">{DayToday()} - {DateToday()}</h5><hr></Col>
                        <Col>
                                <Row class="text-left">
                                    <Col sm="12">
                                        <label><h5><input type="checkbox" id="fb"/> Facebook</h5></label>
                                    </Col>
                                    <Col sm="12">
                                        <label><h5><input type="checkbox" id="tw"/> Twitter</h5></label>
                                    </Col>
                                    <Col sm="12">
                                        <label><h5><input type="checkbox" id="ld"/> LinkedIn</h5></label>
                                    </Col>
                                    <Col sm="12">
                                        <label><h5><input type="checkbox" id="ot"/> Other</h5></label>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col>
                                        <button id="btn_text" on:click={totalChecks} class="pressDown btn">Complete?</button>
                                    </Col>
                                </Row>
                        </Col>
                    </Card>
                </Col>
                <Col sm="5">
                    <h4 class="text-primary font-weight-bold py-2">Your Statistics üìà</h4>
                    <Card body>
                    <Row class="login text-center py-4 ">
                        <Col lg="12">
                            <h5 class="text-primary font-weight-bold">üé® Content created</h5> <span>{contentMadeSoFar}</span>
                        </Col>
                    </Row>
                    <Row class="login text-center">
                        <Col lg="12">
                            <h5 on:click={toggle} class="text-primary font-weight-bold">‚è∞ Time saved ‚ìò</h5> <span>approx. {timesave} min.</span>
                            <Modal isOpen={open} {toggle}>
                                <ModalHeader>
                                    How does your save time calculated?
                                </ModalHeader>
                                <ModalBody>
                                  Time saved is calculated by assuming 1 piece of content creation takes approximately
                                  2 minute of time.
                                </ModalBody>
                                <ModalFooter class="text-left">
                                    <Row>
                                        <Col>
                                    <h6>Time saved = Content Created x 2</h6>
                                        </Col>
                                    </Row>
                                </ModalFooter>
                              </Modal>
                        </Col>
                    </Row>
                    <Row class="login text-center">
                        <Col lg="12">
                            <hr>
                            <br>
                            <h5 id="contentStatusText" class="text-primary font-weight-bold">No content created today :(</h5>
                        </Col>
                    </Row>
                    </Card>
                </Col>
                <Col sm="3">
                    <h4 class="text-primary font-weight-bold py-2">Weekly Calendar üìÖ</h4>
                    <Card body>
                    <Row class="login py-4 text-left">
                        <Col lg="12">
                            <h5 class="text-primary font-weight-bold">{thisWeek()} - Sunday</h5> 
                        </Col>
                        <Col lg="12">
                            <h5 class="text-primary font-weight-bold">{thisWeek(1)} - Monday</h5> 
                        </Col>
                        <Col lg="12">
                            <h5 class="text-primary font-weight-bold">{thisWeek(2)} - Tuesday</h5> 
                        </Col>
                        <Col lg="12">
                            <h5 class="text-primary font-weight-bold">{thisWeek(3)} - Wednesday</h5> 
                        </Col>
                        <Col lg="12">
                            <h5 class="text-primary font-weight-bold">{thisWeek(4)} - Thursday</h5> 
                        </Col>
                        <Col lg="12">
                            <h5 class="text-primary font-weight-bold">{thisWeek(5)} - Friday</h5> 
                        </Col>
                        <Col lg="12">
                            <h5 class="text-primary font-weight-bold">{thisWeek(6)} - Saturday</h5> 
                        </Col>
                    </Row>
                    </Card>
                </Col>
            </Row>
        </Col> 
    </Row>
    <Row class="login text-center py-2">
        <Col>
            <Alert color="success">
                <h4 class="alert-heading">Join the Community of Marketers & Makers!</h4>
                Discuss & Learn more about Content Marketing, Top Growth Strategies, Giveaways, and more!
                <a href="https://www.facebook.com/groups/614010692879949/" target="_blank" class="alert-link text-capitalize" use:link>
                Join us on Facebook
                </a>.
            </Alert>
        </Col>
    </Row>

    <Row class="login text-center py-5">
        <Card body>
        <Col lg="12" class="my-2">
            <h5 class="text-primary font-weight-bold">My Account:-</h5>
        </Col>
            <Col>
                <Container fluid>
                <Table class="text-left">
                    <tbody>
                        <tr>
                            <td>
                                <Label for="emailID">Name:-</Label>
                            </td>
                            <td>
                                <input class="w-50" type="text" value={displayName} disabled/>
                            </td>
                        </tr>
                <tr>
                    <td>
                        <Label for="emailID">Email:-</Label>
                    </td>
                    <td>
                        <input class="w-50" type="text" value={email} disabled/>
                    </td>
                </tr>
                <tr>
                        <td><Label for="userName">Account Type:-</Label></td>
                        <td>
                            {#if isPro}<span class="text-primary">Pro üòé</span>{:else}<span class="text-primary">FREE ü§†</span>{/if}
                        </td>
                </tr>
                {#if isPro}
                <tr>
                        <td><Label for="userName">Subscription / Transactions Info. :-</Label></td>
                        <td>
                            <a href="#" on:click={emailUserTransactions}>Email Me</a>
                        </td>
                </tr>
                {/if}
                <tr>
                    <td>
                        <a href="/Support" use:link><Button class="w-25" color="primary">Need Help?</Button></a>
                    </td>       
                    <!--td>
                                <Button color="danger" on:click={ () => {
                                    auth.signOut()
                                    backToHome()
                                } }>Log Out</Button>
                    </td-->
                </tr>
                </tbody>
                </Table>
            </Container>
            </Col>
        </Card>
        </Row>
        {#if !isPro}
        <Row class="login text-left">
            <Col sm="12">
                <Alert color="success">
                    <h4 class="alert-heading">You are missing out!</h4>
                    For the best experience, We recommend you upgrade to Pro plan.<br/>
                    <Row class='mt-2'>
                        <Col>
                            <Button class="text-uppercase" block color="primary" on:click={openMCheckout}>Upgrade for $6/mo</Button>
                        </Col>
                        <Col>
                            <Button class="text-uppercase" block color="primary" on:click={openYCheckout}>Upgrade for $60/yr</Button>
                        </Col>
                    </Row>
                  </Alert>
            </Col>
        </Row>
    {/if}
{/if}
</Container>