	beforeUpdate(() => {
	console.log("beforeUpdate was called");
		//setting default values to canvas dimensions
		width = 720;
		height = 350;
		// then create default one layer
		//layer = new Konva.Layer();
		
	});

	afterUpdate(() =>{
		console.log("AfterUpdate was called");
		

		function fitStageIntoParentContainer() {
        let lcontainer = document.getElementById('stage-parent');

        // now we need to fit stage into parent
        var containerWidth = document.getElementById('stage-parent').offsetWidth;
        // to do this we need to scale the stage
        var scale = containerWidth / width;

        stage.width(width * scale);
        stage.height(height * scale);
        stage.scale({ x: scale, y: scale });
        stage.draw();
      }

      fitStageIntoParentContainer();
      // adapt the stage on any window resize
      window.addEventListener('resize', fitStageIntoParentContainer);

	  let tooltipLayer = new Konva.Layer();
		let tooltip = new Konva.Label({
			opacity: 0.75,
			visible: false,
			listening: false
		  });
		/*tooltip.add(
			new Konva.Tag({
			  fill: 'black',
			  pointerDirection: 'down',
			  pointerWidth: 10,
			  pointerHeight: 10,
			  lineJoin: 'round',
			  shadowColor: 'black',
			  shadowBlur: 10,
			  shadowOffset: 10,
			  shadowOpacity: 0.2
			})
		  );
		tooltip.add(
		  new Konva.Text({
			text: 'Test',
			fontFamily: 'Calibri',
			fontSize: 18,
			padding: 5,
			fill: 'white'
		  })
		);*/
		
		tooltipLayer.add(tooltip);

		// were can we snap our objects?
		function getLineGuideStops(skipShape) {
			// we can snap to stage borders and the center of the stage
			let vertical = [0, stage.width() / 2, stage.width()];
			let horizontal = [0, stage.height() / 2, stage.height()];
			// and we snap over edges and center of each object on the canvas
			stage.find('.object').forEach(guideItem => {
			  if (guideItem === skipShape) {
				return;
			  }
			  let box = guideItem.getClientRect();
			  // and we can snap to all edges of shapes
			  vertical.push([box.x, box.x + box.width, box.x + box.width / 2]);
			  horizontal.push([box.y, box.y + box.height, box.y + box.height / 2]);
			});
			return {
			  vertical: vertical.flat(),
			  horizontal: horizontal.flat()
			};
		  }
		  // what points of the object will trigger to snapping?
		  // it can be just center of the object
		  // but we will enable all edges and center
		function getObjectSnappingEdges(node) {
			let box = node.getClientRect();
			return {
			  vertical: [
				{
				  guide: Math.round(box.x),
				  offset: Math.round(node.x() - box.x),
				  snap: 'start'
				},
				{
				  guide: Math.round(box.x + box.width / 2),
				  offset: Math.round(node.x() - box.x - box.width / 2),
				  snap: 'center'
				},
				{
				  guide: Math.round(box.x + box.width),
				  offset: Math.round(node.x() - box.x - box.width),
				  snap: 'end'
				}
			  ],
			  horizontal: [
				{
				  guide: Math.round(box.y),
				  offset: Math.round(node.y() - box.y),
				  snap: 'start'
				},
				{
				  guide: Math.round(box.y + box.height / 2),
				  offset: Math.round(node.y() - box.y - box.height / 2),
				  snap: 'center'
				},
				{
				  guide: Math.round(box.y + box.height),
				  offset: Math.round(node.y() - box.y - box.height),
				  snap: 'end'
				}
			  ]
			};
		  }
		  // find all snapping possibilities
		function getGuides(lineGuideStops, itemBounds) {
			let resultV = [];
			let resultH = [];
			lineGuideStops.vertical.forEach(lineGuide => {
			  itemBounds.vertical.forEach(itemBound => {
				let diff = Math.abs(lineGuide - itemBound.guide);
				// if the distance between guild line and object snap point is close we can consider this for snapping
				if (diff < GUIDELINE_OFFSET) {
				  resultV.push({
					lineGuide: lineGuide,
					diff: diff,
					snap: itemBound.snap,
					offset: itemBound.offset
				  });
				}
			  });
			});
			lineGuideStops.horizontal.forEach(lineGuide => {
			  itemBounds.horizontal.forEach(itemBound => {
				let diff = Math.abs(lineGuide - itemBound.guide);
				if (diff < GUIDELINE_OFFSET) {
				  resultH.push({
					lineGuide: lineGuide,
					diff: diff,
					snap: itemBound.snap,
					offset: itemBound.offset
				  });
				}
			  });
			});
		  let guides = [];
			// find closest snap
		  let minV = resultV.sort((a, b) => a.diff - b.diff)[0];
		  let minH = resultH.sort((a, b) => a.diff - b.diff)[0];
		  if (minV) {
			  guides.push({
				lineGuide: minV.lineGuide,
				offset: minV.offset,
				orientation: 'V',
				snap: minV.snap
			  });
			}
		  if (minH) {
			  guides.push({
				lineGuide: minH.lineGuide,
				offset: minH.offset,
				orientation: 'H',
				snap: minH.snap
			  });
			}
		  return guides;
		}
		function drawGuides(guides) {
			guides.forEach(lg => {
			  if (lg.orientation === 'H') {
				let line = new Konva.Line({
				  points: [-6000, lg.lineGuide, 6000, lg.lineGuide],
				  stroke: 'rgb(0, 161, 255)',
				  strokeWidth: 1,
				  name: 'guid-line',
				  dash: [4, 6]
				});
				layer.add(line);
				layer.batchDraw();
			  } else if (lg.orientation === 'V') {
				let line = new Konva.Line({
				  points: [lg.lineGuide, -6000, lg.lineGuide, 6000],
				  stroke: 'rgb(0, 161, 255)',
				  strokeWidth: 1,
				  name: 'guid-line',
				  dash: [4, 6]
				});
				layer.add(line);
				layer.batchDraw();
			  }
			});
		  }
		layer.on('dragmove', function(e) {
			// clear all previous lines on the screen
			layer.find('.guid-line').destroy();
			// find possible snapping lines
			let lineGuideStops = getLineGuideStops(e.target);
			// find snapping points of current object
			let itemBounds = getObjectSnappingEdges(e.target);
			// now find where can we snap current object
			let guides = getGuides(lineGuideStops, itemBounds);
			// do nothing of no snapping
			if (!guides.length) {
			 				 return;
							}
		drawGuides(guides);
		// now force object position
		guides.forEach(lg => {
			  switch (lg.snap) {
				case 'start': {
				  switch (lg.orientation) {
					case 'V': {
					  e.target.x(lg.lineGuide + lg.offset);
					  break;
					}
					case 'H': {
					  e.target.y(lg.lineGuide + lg.offset);
					  break;
					}
				  }
				  break;
				}
				case 'center': {
				  switch (lg.orientation) {
					case 'V': {
					  e.target.x(lg.lineGuide + lg.offset);
					  break;
					}
					case 'H': {
					  e.target.y(lg.lineGuide + lg.offset);
					  break;
					}
				  }
				  break;
				}
				case 'end': {
				  switch (lg.orientation) {
					case 'V': {
					  e.target.x(lg.lineGuide + lg.offset);
					  break;
					}
					case 'H': {
					  e.target.y(lg.lineGuide + lg.offset);
					  break;
					}
				  }
				  break;
				}
			  }
			});
		  });
		layer.on('dragend', function(e) {
		  // clear all previous lines on the screen
		  layer.find('.guid-line').destroy();
		  layer.batchDraw();
		});
		layer.draw();
		
		stage.on('click tap', function(e) {
		  // if click on empty area - remove all transformers
		  if (e.target === stage) {
			stage.find('Transformer').destroy();
			layer.draw();
			return;
		  }
		  // do nothing if clicked NOT on our rectangles
		  if (!e.target.hasName('object')) {
			return;
		  }
		  // remove old transformers
		  // TODO: we can skip it if current rect is already selected
		  stage.find('Transformer').destroy();
		  // create new transformer
		  let tr = new Konva.Transformer();
		  layer.add(tr);
		  tr.attachTo(e.target);
		  layer.draw();
		  let mousePos = e.target.getStage().getPointerPosition();
		  tooltip.position({
			  x: mousePos.x,
			  y: mousePos.y - 5
		  });
		  tooltip
			  .getText()
			  .text('object: ' + e.target.id() + ', color: ' + e.target.fill());
			tooltip.show();
			tooltipLayer.batchDraw();
		  });

		  layer.on('click tap', function(e) {
		  // if click on empty area - remove all transformers
		  if (e.target === layer) {
			removeTransformer();
			return;
		  }
		  // do nothing if clicked NOT on our shapes or text
		  if (!e.target.hasName('object')) {
			return;
		  }
		});
		
		stage.on('mouseout', function(evt) {
		  tooltip.hide();
		  tooltipLayer.draw();
		});

		// add the layer to the stage
		stage.add(tooltipLayer);
		stage.add(layer);
		// draw the image
		layer.draw();
	});

	onMount(() => {
	//	console.log("OnMount was called");
	//	console.log("Checking for Pro Access");
		proAccessCheck();
		// first we need to create a stage
		stage = new Konva.Stage({
			container: 'konva-stage',   // id of container <div>
			width: width,
			height: height
		});
		bglayer = new Konva.Layer();
		stage.add(bglayer)
		bglayer.moveToBottom()
		
		});
	</script>
<div>
	<Container class="mt-2" fluid>
	<Row class="mt-2">
		<!-- Canvas / Stage / Layer Component-->
		<Col lg="6">
			<div id="stage-parent">
					<div id="konva-stage"></div>
			</div>
			<Row class="mt-2">
				<Col>
				<a class="pressDown btn btn-block btn-lg" on:click={toggleLg}>Select and Download</a>
				<Modal class=".modal-dialog-centered" isOpen={open} {toggle} {size}>
					<ModalHeader {toggle}>Select Your Platforms</ModalHeader>
					<ModalBody>
					  <Row>
						  <Col>
							<CustomInput
							type="checkbox"
							name="fb" id="fb"><b>Facebook</b> (1200x717px)</CustomInput>
						  </Col>
						  <Col>
							<CustomInput
							type="checkbox"
							name="tw" id="tw" label="Twitter (506 x 253)" />
						  </Col>
					  </Row>
					  <Row>
						  <Col>
							<CustomInput
							type="checkbox"
							name="ln" id="ln" label="LinkedIn (1536 x 768)" />
						  </Col>
						  <Col>
							<CustomInput
							type="checkbox"
							name="og" id="og" label="Best Fit (For everywhere)" />
						  </Col>
					  </Row>
					  <Row>
						  <Col>
						  	<div class="centerInfoText">
							  <hr/>
								{#if toRemix}
								<span>All designs you select WILL be remixed and unique. You can change this setting in "Remix" Tab.</span>
								{:else}
								<span>All designs you select WILL NOT be remixed and unique. You can change this setting in "Remix" Tab.</span>
								{/if}
						  	</div>
						  </Col>
					  </Row>
					</ModalBody>
					<ModalFooter>
					  <Button color="primary" on:click={selectAndDownload}>
						Download My Files
					  </Button>
					  <Button color="secondary" on:click={toggle}>
						Cancel
					  </Button>
					</ModalFooter>
				  </Modal>
				</Col>
			</Row>
		</Col>


		<Col lg="5" offset="1">
		<Card body>
		<Row>
		<Col>
			<Tabs>
				<TabList>
					<Tab><h4>‚úè Text</h4></Tab>
					<Tab><h4>üñº Background</h4></Tab>
					<Tab><h4>üí° Idea</h4></Tab>
					<Tab><h4>‚ú®Remix</h4></Tab>
				</TabList>
			
				<TabPanel>
				<!-- Text Editor Component-->
				<Row class="mt-3">
					<Col lg="5" offset="1">
						<Label for="inputText">Enter Text:-</Label>
					</Col>
					<Col lg="5">
						<Input type="textarea" placeholder="Happy Birthday!" name="userText" id="userText"/>
					</Col>
				</Row>
				<Row class="mt-3">
					<Col lg="5" offset="1">
						<Label for="inputColor">Font Color:-</Label>
					</Col>
					<Col lg="5">
						<Input
								type="color"
								name="color"
								id="userTextColor"
								placeholder="colorPicker" />
					</Col>
				</Row>
				<Row>
					<Col lg="5" offset="1">
						<Label for="inputFont">Font Type:-</Label>
					</Col>
					<Col lg="5">
						<Input type="select" id="userTextFonts" name="userTextFonts">
							<option value="verdana">Verdana</option>
							<option selected value="Calibri">Calibri</option>
							<option value="sans-serif">Sans-serif</option>
							<option value="cursive">Cursive</option>
							<option value="fantasy">Fantasy</option>
							<option value="monospace">Monospace</option>
							<option value="'Helvetica Neue'">Helvetica Neue</option>
							<option value="Helvetica">Helvetica</option>
							<option value="Arial">Arial</option>
						</Input>
					</Col>
				</Row>
				<Row class="mt-3">
					<Col lg="5" offset="1">
						<Label for="inputFontSize">Font Size:-</Label>
					</Col>
					<Col lg="5">
						<Input type="number" placeholder="30" min="0" max="100" name="userTextSize" id="userTextSize" value="25"/>
					</Col>
				</Row>
				<Row class="mt-3">
					<Col sm="12">
						<Button class="m-1" block color="primary" size="lg" on:click={addText}>Add New Text</Button>
					</Col>
				</Row>
				<Row class="text-center">
					<Col>
						<Button class="m-1" block outline color="danger" size="lg" on:click={deleteLastElement}>Delete Last Element</Button>
					</Col>
					<Col>
						<Button class="m-1" block outline color="info" size="lg" on:click={removeTransformer}>Unhighlight Text</Button>
					</Col>
				</Row>
				</TabPanel>
	
				<TabPanel>
					<Row class="mt-3">
						<Col lg="5" offset="1">
							<Label for="inputColor">Add Background Color:-</Label>
						</Col>
						<Col lg="6">
							<Input
								type="color"
								name="bgcolor"
								value="#FFFFFF"
								id="userBGcolor"
								on:change={addBgColor}
								placeholder="colorPicker" />
						</Col>
					</Row>
					<!--<Row class="mt-3">
						<Col lg="6">
							<Button class="m-1" block color="primary" size="lg" on:click={addBgColor}>Apply BG</Button>
						</Col>
					</Row>-->
					<Row>
						<Col>
							<Label for="randomBg">Apply Random Background:-</Label>
						</Col>
					</Row>
					<Row class="mt-3">
						<Col>
							<Button class="m-1" block color="success" size="lg" on:click={randomBGcolor}>Color</Button>
						</Col>
						<Col>
							<Button class="m-1" block color="warning" size="lg" on:click={generateShapes}>Shapes</Button>
						</Col>
						<!--<Col sm="12">
							<Button class="m-1" block color="warning" size="lg" on:click={remixPattern}>Pattern</Button>
						</Col>-->
					</Row>
					<Row class="mt-3">
						<Col>
							<Label for="randomBg">Upload Image (Please apply Background before uploading an Image):-</Label>
						</Col>
						<Col sm="12">
							<Button class="m-1" block color="info" size="lg" on:click={openUploadPopUp}>Upload Image</Button>
							<Input type="file" name="file" id="userImgUpload" on:change={addIMGtoStage} hidden/>
						</Col>
						</Row>
					<Row>
							<Col>
								<Label for="randomBg">Clear Canvas:-</Label>
							</Col>
							<Col sm="12">
								<Button class="m-1" block outline color="danger" size="lg" on:click={clearCanvas}>Clear</Button>
							</Col>
					</Row>
				</TabPanel>
				<TabPanel>
					<Row class="mt-3">
						<Col>
							<h3>Content Inspiration</h3>
						</Col>
						<Col>
							<p>
								Get inspiration for Content and Designs to help you get started. Click on the button to generate random idea.
							</p>
						</Col>
					</Row>
					{#if allowProAccess}
					<Row>
						<Col sm="12" md="{{ size: 6, offset: 3 }}">
						<Button class="m-1" block color="primary" size="lg" on:click={generateIdea}>Generate Idea</Button>
						</Col>
					</Row>
					<Row>
						<Col>
							<hr/>
							<div class="marketingTopic">
								{marketingIdea}
							</div>
						</Col>
					</Row>
					{:else}
					<Row>
						<Col sm="12" class='text-center'>
							<h4 class="text-warning">Access this feature by upgrading to PRO.</h4>
						</Col>
						<Col sm="12" class='m-2 text-center'>
							<a href="/Login" replace><Button class="w-25" color="primary">Upgrade Now</Button></a>
							<h6>Free for a month!</h6>
						</Col>
					</Row>
					{/if}
				</TabPanel>
				<TabPanel>
					<Row class="mt-3">
						<Col>
							<h3>Remix Content</h3>
						</Col>
						<Col>
							<p>
								Remix Your Content for all Designs for different Social Media Channels.
							</p>
						</Col>
					</Row>
					{#if allowProAccess}
					<!--Row>
						<Col>
							<Card>
							Remix will USE your design and generate DIFFERENT styles of it. You can toggle it here. This is ON by Default.	
							</Card>
						</Col>
					</Row-->
					<Row>
						<Col>
						<Button class="m-1" block color="info" size="lg" on:click={remixCurrentDesign}>Remix Design.</Button>
						</Col>
						<Col>
						<Button class="m-1" block color="primary" size="lg" on:click={()=>{toRemix=!toRemix;}}>Toggle Remix.</Button>
						</Col>
						<Col sm="12">
							<hr/>
							<div class="marketingTopic"><h4>Status:- {#if toRemix}
								I want to remix the Content.
							{:else}
							I want to keep the same Design.
							{/if}</h4></div>
						</Col>
					</Row>
					{:else}
					<Row>
						<Col sm="12" class='text-center'>
							<h4 class="text-warning">Access this feature by upgrading to PRO.</h4>
						</Col>
						<Col sm="12" class='m-2 text-center'>
							<a href="/Login" replace><Button class="w-25" color="primary">Upgrade Now</Button></a>
							<h6>Free for a month!</h6>
						</Col>
					</Row>
					{/if}
				</TabPanel>
			</Tabs>
		</Col>
		</Row>
		</Card>
			</Col>
		</Row>
				<Card body>
					<Container fluid>
					<Row>
						<Col>
							<h5 class="text-info">Tips to get started</h5>
						</Col>
					</Row>
					<Row>
						<Col md="3">
							<p>1. Start by choosing a üñº<b>Background</b>. You can take idea on what to make from üí°<b>Idea</b>.</p>
						</Col>
						<Col md="3">
							<p>2. Add a <b>Logo</b> with Upload Image option in the bottom of üñº<b>Background</b> tab.</p>
						</Col>
						<Col md="3">
							<p>3. Then write some text, customize and Add with the ‚úè <b>Text</b> option. Double-click the text to resize it.</p>
						</Col>
						<Col md="3">
							<p>4. Add shapes, or enable/disable ‚ú® <b>Remix</b> option as per your need. Download and share your amazing content!</p>
						</Col>
					</Row>
				</Container>
				</Card>
		</Container>
	</div>
