<svelte:head>
	<link rel="stylesheet" href="https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/flatly/bootstrap.min.css">
</svelte:head>
<style>
    Link {
        color:lime;
        text-decoration: none;
    }
</style>
<script>
    let userSignedIn = false;
 import { auth, googleProvider, twitterProvider, db, firebaseAnalytics } from './firebase';
 auth.onAuthStateChanged(function(user) {
  if (user) {
    userSignedIn = true;
  } else {
    userSignedIn = false;
  }
});

  import { Router, Link,link, Route } from "svelte-routing";
  import {Row, Container, Button, Col,Collapse,Navbar,NavbarToggler,NavbarBrand,Nav,NavItem,NavLink,UncontrolledDropdown,DropdownToggle,DropdownMenu,DropdownItem} from 'sveltestrap';
  
  import App from './App.svelte';
  import Home from './Home.svelte';
  import Login from './Login.svelte';
  //import Pricing from './Pricing.svelte';
  import Changelog from './Changelog.svelte';
  import About from './About.svelte';
  import PrivacyPolicy from './PrivacyPolicy.svelte';
  import TOS from './TOS.svelte';
  import Support from './Support.svelte'
  import Difference from './Difference.svelte';

  export let url = "";

  let isOpen = false;

function handleUpdate(event) {
  isOpen = event.detail.isOpen;
}

</script>
<div>
    <!-- Navbar , logo code <img src="https://i.imgur.com/YqNaeaq.png" alt="logo" height="50" width="50" />-->
            <Router url="{url}">
                <Navbar class="navbar-expand-lg navbar-light">
                    <NavbarBrand href="/"><img src="https://i.imgur.com/YqNaeaq.png" alt="logo" height="70" width="70" /></NavbarBrand>
                    <NavbarToggler on:click={() => (isOpen = !isOpen)} />
                    <Collapse {isOpen} navbar expand="md" on:update={handleUpdate}>
                        <Nav class="ml-auto" navbar>
                        <NavItem>
                            <a class="text-primary text-uppercase font-weight-bold nav-link" href="/#pricing">Pricing</a>
                        </NavItem>
                        <NavItem>
                                <a class="text-primary text-uppercase font-weight-bold nav-link" href="/Changelog">Changelog</a>
                        </NavItem>
                        {#if userSignedIn}
                        <NavItem>
                            <a class="text-success text-uppercase font-weight-bold nav-link" href="/Login">My Account</a>
                        </NavItem>
                        <NavItem>
                            <a href="#"  class="text-danger text-uppercase font-weight-bold nav-link" on:click={ () => {
                                auth.signOut()
                                window.location = "/";
                            } }>Log out</a>
                        </NavItem>
                        {:else}
                        <NavItem>
                            <a class="text-success text-uppercase font-weight-bold nav-link" href="/Login">Sign In</a>
                        </NavItem>
                        {/if}

                        <!--<NavItem>
                            <a class="nav-link" href="/Support" use:link>Support</a>
                        </NavItem>
                        <UncontrolledDropdown>
                            <DropdownToggle nav caret>Legal</DropdownToggle>
                            <DropdownMenu class="bg-primary" right>
                            <DropdownItem><a class="nav-link" href="/PrivacyPolicy" use:link>Privacy Policy</a></DropdownItem>
                            <DropdownItem><a class="nav-link" href="/TOS" use:link>Terms</a></DropdownItem>
                            <DropdownItem><a class="nav-link" href="/About" use:link>About</a></DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>
                        <NavItem>
                            <a href="/Login" replace><Button color="success">Create Design</Button></a>
                        </NavItem>-->
                        </Nav>
                    </Collapse>
                </Navbar>
                <div>
                    <Route path="/" component="{Home}" />
                    <Route path="/Changelog" component="{Changelog}" />
                    <Route path="/Login" component="{Login}" />
                    <Route path="/App" component="{App}" />
                    <Route path="/Support" component="{Support}" />
                    <Route path="/Difference" component="{Difference}" />
                    <Route path="/PrivacyPolicy" component="{PrivacyPolicy}" />
                    <Route path="/TOS" component="{TOS}" />
                    <Route path="/About" component="{About}" />
                  </div>
            </Router>
        </div>
